:root {
  --primary-color: #07352c;
  --secondary-color: #90e2d2;
  --tertiary-color: #1abc9c;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

  /*header*/
  header {
  display: grid;
  grid-template-areas: 'logo nav';
  background-color: #282c34;
  }

.Nav {
  clear: both;
  width: 100%;
  height: auto;
  text-align: right;
  position: relative;
  font-family: 'Aladin', cursive;
}


  header h1 {
    font-weight: bold;
    font-size: 55px;
    margin: 0;
    padding-top: 20px;
    text-shadow: 0 0 5px rgb(0, 0, 0);
    color: #ffffff;
    font-family: 'Aladin', cursive;
  }

  header a {
      text-decoration: none;
      color: white;
  }

  .link {
    display: inline-block;
    margin-block: 2px;
    text-decoration: none;
    color: #ffffff;
    position: relative;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    background: transparent;
    border: none;
    padding: 5px 15px;
    text-transform: uppercase;
    transition: .5s ease;
    width: auto;
    margin-left: 5%;
    letter-spacing: 0.05em;
    font-size: 25px;
  }
  
  .link::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 2px;
    width: 0;
    background-color: #ffc506;
    transition: .5s ease;
  }
  
  .link:hover {
    transition-delay: .5s;
  }
  
  .link:hover::before {
    width: 100%;
  }
  
  .link::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    height: 0;
    width: 100%;
    background-color: #ffc506;
    transition: .4s ease;
    z-index: -1;
  }
  
  .link:hover::after {
    height: 100%;
    transition-delay: 0.4s;
    color: aliceblue;
  }



/*end header*/

h1 {
  color: #ffffff;
  
}

h4 {
  text-align: center;
  font-size: 50px;
  color: #272133;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  text-decoration: underline;
  cursor: default;
