}


.card-body {
  background: lightgray;
  border: 1px solid #272133;
  box-shadow: 0 7px 25px rgb(0, 0, 0);
 }

 .card {
  overflow: hidden;
  box-shadow: 0 2px 4px black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: center;
  color: #fff;
  flex: 33.33%;
}

.card-title {
  padding: .5rem;
  font-weight: bolder;
  font-size: 17px;
  text-transform:uppercase;
  color: #272133;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  font-family: 'Aladin', cursive;
  }

  .project-img {
    height: 12rem;
    width: 100%;
    border: 1px solid #272133;
    object-fit: cover;
    }

  .container {
    display: grid;
    grid-template-columns: repeat(auto-fit,
    minmax(12rem, 20rem));
    padding: 10px 10px 10px 10px;
    justify-content: space-around;
  }  

  .row {
    flex: 1;
    
  }

  .project {
    text-align: center;
    font-size: 55px;
    color: #272133;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    text-decoration: underline;
  }

  form {
    max-width: 480px; 
    margin: 50px auto;
    background-color: #49515f;
    border:1px solid black ;
    font-family: 'Aladin', cursive;
}

label {
  color: #fff;
}

  .feedback {
    color: #ffffff;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    font-family: 'Aladin', cursive;
    font-size: 18px;
    font-weight: 500;
    border-radius: 5px;
    line-height: 22px;
    background-color: transparent;
    border: 2px solid #fff;
    transition: all 0.3s;
    padding: 13px;
    margin-bottom: 15px;
    width: 100%;
    box-sizing: border-box;
    outline: 0;
  }

  .feedback:focus { border: 2px solid #cc6666;}

  textarea {
    height: 150px;
    line-height: 150%;
    resize: vertical;
  }

  [type='submit'] {
    width: 100%;
    background: #cc6666;
    border-radius: 5px;
    border: 0;
    cursor: pointer;
    color: #272133;
    font-size: 24px;
    padding-top: 10px;
    padding-bottom: 10px;
    transition: all 0.3s;
    margin-top: -4px;
    font-weight: 700;
    font-family: 'Aladin', cursive;
  }

  [type='submit']:hover {background: #cc4949;}

  h4,h2 {
    text-align: center;
    font-size: 50px;
    font-family: 'Aladin', cursive;
  }

  

  footer {
    font-size: 48px;
    justify-content: center;
    background-color: #282c34;
    padding-right: calc(1rem * 0.5);
    padding-left: calc(1rem * 0.5);
  }
  
  footer a {
    color: #cc6666;
    opacity: 0.5;
    margin: 10px 20px;
    transition: all 0.3s ease-in-out;
    size: 50px;
  }
  
  footer a:hover {
    opacity: 1;
  }

  .px-1 {
    padding-right: calc(1rem * 0.5);
    padding-left: calc(1rem * 0.5);
  }

  .navActive {
    color: #cc6666;
    cursor: default;
    text-shadow: 3px 3px 15px rgba(255, 255, 255, 0.5);
  }

  .social-container {
    padding: 25px 50px;
    text-align: center;
  }

  a.social {
    margin: 0 1rem;
    transition: transform 250ms;
    display: inline-block;
  }

  a.social:hover {
    transform: translateY(-2px);
  }

  a.linked {
    color:#ffffff;
  }

  a.github {
    color: #fff;
  }